import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { DxDataGridComponent } from 'devextreme-angular';
import { DxoColumnChooserComponent } from 'devextreme-angular/ui/nested';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit, AfterViewInit {
  title = 'grid-delete-item';

  @ViewChild(DxDataGridComponent) dxGridComponent?: DxDataGridComponent;

  @ViewChild(DxoColumnChooserComponent)
  dxColumnChooser?: DxoColumnChooserComponent;

  ngOnInit() {
    this.dxColumnChooser?.instance.options({
      useNative: false,
      showScrollbar: 'always',
    });
  }

  ngAfterViewInit() {
    this.dxColumnChooser?.instance.options({
      useNative: false,
      showScrollbar: 'always',
    });
  }

  absenceTypes = [
    {
      absenceTypeID: 1,
      absenceName: 'Jury Duty',
    },
    {
      absenceTypeID: 2,
      absenceName: 'Holiday',
    },
    {
      absenceTypeID: 3,
      absenceName: 'Vacation',
    },
  ];

  absenceDataSource: absence[] = [];

  createDescriptionDisplayString(item: absence): string {
    return item.absenceName;
  }

  async onDelete(event: unknown): Promise<void> {
    const rowEvent = event as {
      row?: {
        dataIndex?: number;
        rowIndex?: number;
        isEditing?: boolean;
        isNewRow?: boolean;
      };
    };

    if (rowEvent?.row?.isEditing === true && rowEvent?.row?.isNewRow === true) {
      console.log(
        'Canceling the edit - this is a new row that has not been saved'
      );
      this.dxGridComponent?.instance.cancelEditData();
    } else {
      if (
        rowEvent?.row?.rowIndex != null &&
        rowEvent?.row?.rowIndex !== undefined
      ) {
        console.log(
          'Number of rows before delete: ' + this.absenceDataSource.length
        );

        const keyIndex = this.dxGridComponent?.instance.getKeyByRowIndex(
          rowEvent?.row?.rowIndex
        );

        this.dxGridComponent?.instance.saveEditData().then(() => {
          const rowIndex = this.dxGridComponent?.instance.getRowIndexByKey(
            keyIndex
          );
          this.dxGridComponent?.instance.cancelEditData();

          if (rowIndex !== undefined) {
            this.dxGridComponent?.instance.deleteRow(rowIndex);
          }

          this.dxGridComponent?.instance.refresh();
        });

        console.log(
          'Number of rows after delete: ' + this.absenceDataSource.length
        );
      } else {
        console.log('No Row Index returned in event');
      }
    }
  }
}

export interface absence {
  absenceTypeID: number;
  absenceName: string;
  time: number;
  note: string;
  hiddenColumn1?: string;
  hiddenColumn2?: string;
  hiddenColumn3?: string;
  hiddenColumn4?: string;
  hiddenColumn5?: string;
  hiddenColumn6?: string;
  hiddenColumn7?: string;
  hiddenColumn8?: string;
}
